.about{
    width: 100%;
    height: 90vh;
    display: flex;
    flex-direction: column;
}

.aboutTop{
    width: 100%;
    height: 50%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.aboutBottom{
    display: flex;
    flex-direction: column;
    height: 55%;
    align-items: center;
    margin-top: 10px;
}

.aboutBottom p{
    width: 1200px;
    font-size: 15px;
}

.about .aboutBottom h1{
    font-weight: 400;
    font-size: 30px;
    color: black;
    height: 50px;
    display: flex;
    justify-content: center;
    transform: translate(-40px);
}